.main_div {

    display : flex;
    justify-content: space-around;
    /* border:1px solid white; */
    box-shadow:  rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;;
    gap:10px;
    /* align-items: center; */
    width:100%;
    position: fixed; 
    /* background-color: #0fd09c; */
     z-index: 1;
     background-color: maroon;  
}

.left_div{
    display : flex;
    /* justify-content: space-evenly; */
    gap:8px;
    /* border:1px solid white; */
     align-items: center;
     margin: 20px;
     color: white;
     
}
.left_div >div {
    margin: 20px;
    font-size: 26px;
    font-weight: bolder;
    line-height : 26px;
    
    
}

.left_div >div :hover {
    color: #4cb398;
    text-decoration: underline;
}

.right_div{
    display : flex;
    gap:70px;
    font-size: 20px;
    font-weight: 600;
    /* border:1px solid white; */
    align-items: center;
    color: white;
}
.right_div >div :hover {
    color: #4cb398;
    text-decoration: underline;
}